import React, {Component} from 'react'
import {connect} from 'react-redux'

import Grid from '@material-ui/core/Grid';
import CyberNavbar from "../../layout/navbar/CyberNavbar";
import PropTypes from "prop-types";

import {BRANCH_ANIME, BRANCH_MANGA} from "../../../globals";
import {filterTitles, getFiltersFromBranch, getItemsPerPageFromBranch} from "../../browse/functions";
import Loading from "../../layout/common/Loading";
import {ApiResult, User} from "../../../models";
import {getBrowseGridListCols, isEmpty} from "../../functions";
import {AnimeListStatusButtons} from "./anime/tableView";
import {MangaListStatusButtons} from "./manga/tableView";

import '../../../assets/css/list.css'
import {UserListDesignSettingsDialog} from "./user_settings/userListDesignSettings";
import {UserListCardsView, UserListTableView} from "./utils";
import {
    formatUserListDesignSettingsFromBranch,
    getInitialSelected,
    getUserListDesignSettingsFromBranch
} from "./user_settings/settings_functions";
import {Pagination} from "@material-ui/lab";
import {playClickSound} from "../../layout/sounds";
import GoToPageDialog from "../../layout/pagination/goToPageDialog";


class UserList extends Component {
    static propTypes = {
        /** params of the page, to get the user list id */
        params: PropTypes.object.isRequired,
        /** Redux prop of titles_loading */
        titles_loading: PropTypes.bool.isRequired,
        /** Redux prop of list_owner */
        list_owner: PropTypes.bool.isRequired,
        /** Redux prop of filtered_titles_loaded */
        filtered_titles_loaded: PropTypes.bool.isRequired,
        /** Redux prop of filtered_titles */
        filtered_titles: PropTypes.object.isRequired,
        /** Redux prop of user_entry_statuses */
        user_entry_statuses: PropTypes.object,
        /** Redux prop of title_filters */
        title_filters: PropTypes.object,
    };

    constructor() {
        super(...arguments);
        this.closePageDialog = this.closePageDialog.bind(this);
        this.goToPage = this.goToPage.bind(this);
        this.setSelected = this.setSelected.bind(this);
        this.state = {
            list_pk: parseInt(this.props.params.id),
            selected: getInitialSelected(this.props.branch),
            current_page: 1,
            items_per_page: getItemsPerPageFromBranch(this.props.branch),
            page_dialog_open: false,
            gridlist_cols: getBrowseGridListCols(),
        };
    }

    componentDidMount() {
        document.title = `${this.props.visited_user[User.username]}'s ${this.props.branch} List`;
        window.scrollTo(0, 0);
        filterTitles(this.props.branch, 1, this.props.title_filters, null,
            {user_list_pk: this.state.list_pk, user_list_filter: this.state.selected});
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevState.selected !== this.state.selected) {
            this.setState({current_page: 1});
            filterTitles(this.props.branch, 1, this.props.title_filters, null,
                {user_list_pk: this.state.list_pk, user_list_filter: this.state.selected});
        }
        if (prevProps.params.id !== this.props.params.id) {
            window.scrollTo(0, 0);
            this.setState({list_pk: parseInt(this.props.params.id)}, () => {
                this.props.getUser(this.state.list_pk);
                filterTitles(this.props.branch, 1, this.props.title_filters, null,
                    {user_list_pk: this.state.list_pk, user_list_filter: this.state.selected});
            });
        }
    }

    closePageDialog() {
        this.setState({page_dialog_open: false})
    }

    goToPage(page) {
        if (page > 0 && page <= Math.ceil(this.props.filtered_titles[ApiResult.count] / 100)) {
            this.setState({current_page: page});
            filterTitles(this.props.branch, page, this.props.title_filters, null,
                {user_list_pk: this.state.list_pk, user_list_filter: this.state.selected});
        }
    }

    setSelected(selected) {
        this.setState({selected: selected})
    }

    render() {
        const list = {
            user_list_pk: this.state.list_pk,
            user_list_filter: this.state.selected
        };
        const user_settings = getUserListDesignSettingsFromBranch(this.props.branch,
            this.props.user_anime_list_design_settings, this.props.user_manga_list_design_settings);
        const design_settings = formatUserListDesignSettingsFromBranch(this.props.branch, this.props.list_owner,
            this.props.visited_user, user_settings);

        const titles_container_styles = {
            marginTop: design_settings.list_titles_margin_top,
            paddingLeft: design_settings.list_titles_padding_left,
            paddingRight: design_settings.list_titles_padding_right,
        }

        return <>
            <CyberNavbar/>
            <Grid id="User-List" container style={{
                paddingTop: design_settings.list_margin_top,
                backgroundImage: design_settings.background_image_show ?
                    `url("${design_settings.background_image_url}")` : 'none',
                backgroundSize: design_settings.background_image_size,
                backgroundAttachment: design_settings.background_image_attachment,
                backgroundPositionX: design_settings.background_image_position_x,
                backgroundPositionY: design_settings.background_image_position_y,
                backgroundRepeatX: design_settings.background_image_repeat_x,
                backgroundRepeatY: design_settings.background_image_repeat_y,
            }}>
                {this.props.branch === BRANCH_ANIME ?
                    <AnimeListStatusButtons selected={this.state.selected} setSelected={this.setSelected}/>
                    : this.props.branch === BRANCH_MANGA ?
                        <MangaListStatusButtons selected={this.state.selected} setSelected={this.setSelected}/>
                        : null}

                {getFiltersFromBranch(this.props.branch, null, list)}

                {this.props.filtered_titles_loaded && this.props.visited_user_loaded ? <>
                    <Grid item xs={12} className="list-container">
                        <div className="about-text-container" style={{
                            marginTop: design_settings.about_margin_top,
                        }}>
                            <h2 style={{
                                color: design_settings.about_header_font_color,
                                paddingLeft: design_settings.about_header_left,
                                paddingRight: design_settings.about_header_right,
                                fontFamily: design_settings.about_font_family,
                                fontSize: design_settings.about_header_font_size,
                                textAlign: design_settings.about_header_align,
                                fontStyle: design_settings.about_header_font_style,
                                fontVariant: design_settings.about_header_font_variant,
                                fontWeight: design_settings.about_header_font_weight,
                            }}>
                                {design_settings.about_header}
                            </h2>
                            <pre style={{
                                color: design_settings.about_text_font_color,
                                paddingLeft: design_settings.about_text_left,
                                paddingRight: design_settings.about_text_right,
                                fontFamily: design_settings.about_font_family,
                                fontSize: design_settings.about_text_font_size,
                                textAlign: design_settings.about_text_align,
                                fontStyle: design_settings.about_text_font_style,
                                fontVariant: design_settings.about_text_font_variant,
                                fontWeight: design_settings.about_text_font_weight,
                            }}>
                                {design_settings.about_text}
                            </pre>
                        </div>

                        {design_settings.cover_image_show ?
                            <div className="user-list-cover-img-container" style={{
                                textAlign: design_settings.cover_image_align
                            }}>
                                <img className="cover-img"
                                     src={design_settings.cover_image_url}
                                     style={{
                                         marginTop: design_settings.cover_image_margin_top,
                                         objectFit: design_settings.cover_image_object_fit,
                                         height: design_settings.cover_image_height,
                                         width: design_settings.cover_image_width,
                                         borderTopLeftRadius: design_settings.cover_border_top_left,
                                         borderTopRightRadius: design_settings.cover_border_top_right,
                                         borderBottomLeftRadius: design_settings.cover_border_bottom_left,
                                         borderBottomRightRadius: design_settings.cover_border_bottom_right,
                                     }}
                                />
                            </div> : null}

                        <div className="titles-container" style={titles_container_styles}>
                            {design_settings.list_type_cards ?
                                <UserListCardsView
                                    branch={this.props.branch}
                                    items_per_page={this.state.items_per_page}
                                    current_page={this.state.current_page}
                                    user_entry_statuses={this.props.user_entry_statuses}
                                    filtered_titles={this.props.filtered_titles}
                                    gridlist_cols={this.state.gridlist_cols}
                                    list_owner={this.props.list_owner}
                                    design_settings={design_settings}
                                />
                                : <UserListTableView
                                    branch={this.props.branch}
                                    items_per_page={this.state.items_per_page}
                                    current_page={this.state.current_page}
                                    user_entry_statuses={this.props.user_entry_statuses}
                                    filtered_titles={this.props.filtered_titles}
                                    list_owner={this.props.list_owner}
                                    list={list}
                                    design_settings={design_settings}
                                />}
                        </div>
                        <div className="pagination">
                            <Pagination
                                count={Math.ceil(this.props.filtered_titles[ApiResult.count] / 100)}
                                color="primary"
                                siblingCount={3}
                                page={this.state.current_page}
                                onChange={(type, page) => {
                                    playClickSound();
                                    if (page !== this.state.current_page)
                                        this.goToPage(page);
                                    else
                                        this.setState({page_dialog_open: true})
                                }}
                            />
                            <GoToPageDialog
                                open={this.state.page_dialog_open}
                                current_page={this.state.current_page}
                                total_pages={Math.ceil(this.props.filtered_titles[ApiResult.count] / 100)}
                                goToPage={this.goToPage}
                                onClose={this.closePageDialog}/>
                        </div>
                    </Grid>

                    {this.props.list_owner && !isEmpty(user_settings) ?
                        <UserListDesignSettingsDialog branch={this.props.branch} user_settings={user_settings}/> : null}
                </> : this.props.titles_loading ?
                    <Loading text={`${this.props.branch} List Loading`} offset={250}/> : null}
            </Grid>
            {/*<CyberFooter/>*/}
        </>
    }
}

const mapStateToProps = state => ({
    user: state.auth.user,
    visited_user: state.auth.visited_user,
    visited_user_loaded: state.auth.visited_user_loaded,
    user_loading: state.auth.user_loading,
    entry_loading: state.users.entry_loading, // This is here to rerender the small loading on the update episode btn
    user_anime_list_design_settings: state.users.user_anime_list_design_settings, // This is here to rerender the small loading on the update episode btn
    user_manga_list_design_settings: state.users.user_manga_list_design_settings, // This is here to rerender the small loading on the update episode btn
});

export default connect(mapStateToProps, {})(UserList)
